/* newproducts - flex container -parent element */

#editproducts {
  width: 100%;
  height: 100%;
  font-family: "Roboto", sans-serif;
  display: flex;
  justify-content: center;
  /* align-items: center;      */
  flex-wrap: wrap;
  align-content: center;
}

#epmain-container {
  width: 97%;
  height: calc(100% - 170px);
}

#epmaintitle {
  width: 90%;
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  margin: 20px auto 20px auto;
}

#epmaintitle h1 {
  font-size: 22px;
  font-weight: bold;
  color: #000000;
}

.epform-container {
  /* display: inline-flex; */
  display: flex;
  /* flex-wrap: wrap; */
  align-items: center; /* check*/
  justify-content: center; /*check*/
  width: 73%;
  height: 650px;
  margin: 0 auto 40px 5%;
  box-sizing: border-box;
  overflow-x: hidden;
  /* background-color: red; */
  /* clear: both; */
}

/* .epform-container > * {
    flex: 1 0 300px;
  } */

@media screen and (max-width: 1024px) {
  .epform-container {
    width: 90%;
  }
  .epform-container #epfpage {
    display: flex;
    padding: 0px 50px 0px 50px;
    width: 55%;
  }
  .epform-container #ep-right-page {
    width: 45%;
  }
  #epfpage input {
    min-width: 0px;
    width: 100%;
  }

  #epfpage #btnsEp {
    display: flex;
    flex-direction: column;
  }
  #btnsEp .epcl-button {
    /* margin: 40px 0px 0px 0px; */
    margin: 20px 0px 10px 0px;
    /* width: 180px; */
    width: 100%;
  }
  #btnsEp .eps-button {
    /* margin: 40px 18px 0px 0px; */
    margin: 30px 0px 10px 0px;
    /* width: 180px; */
    width: 100%;
  }
}
@media screen and (max-width: 768px) {
  .epform-container {
    display: flex;
    /* flex-direction: column; */
    height: 650px;
    width: 90%;
  }
  .epform-container #epfpage {
    display: flex;
    align-items: center;
    padding: 0px 50px 0px 50px;
    height: 650px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 10px;
    width: 63%;
  }
  .epform-container #ep-right-page {
    width: 37%;
    height: 650px;
    border-top-left-radius: 0px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0px;
  }
  #epfpage input {
    width: 100%;
  }
  #epfpage #btnsEp {
    display: flex;
    flex-direction: column;
  }
  #btnsEp .eps-button {
    margin-top: 30px;
    width: 100%;
    margin-right: 0px;
  }
  #btnsEp .epcl-button {
    margin: 20px 0px 10px 0px;
    width: 100%;
  }
}

@media screen and (max-width: 426px) {
  #epmain-container {
    height: calc(100% - 280px);
  }
  .epform-container {
    display: flex;
    flex-direction: column;
    height: 800px;
    width: 90%;
  }
  .epform-container #epfpage {
    display: flex;
    align-items: center;
    padding: 0px 50px 0px 50px;
    height: 650px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
    width: 100%;
  }
  .epform-container #ep-right-page {
    width: 100%;
    height: 150px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-left-radius: 10px;
  }
  #epfpage input {
    /* min-width: 0px; */
    width: 100%;
  }
  #epfpage #btnsEp {
    display: flex;
    /* align-items: center; */
    flex-direction: column;
  }
  #btnsEp .epcl-button {
    margin: 20px 0px 10px 0px;
    width: 100%;
  }
  #btnsEp .eps-button {
    margin-top: 30px;
    width: 100%;
    margin-right: 0px;
  }
}

#epfpage {
  display: flex;
  /* flex-flow: row wrap; */
  float: left;
  align-items: center;
  justify-content: flex-start;
  width: 53%;
  height: 100%;
  background-color: #fff;
  box-sizing: border-box;
  padding: 0px 0px 0px 50px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  overflow-x: hidden;
}

.eplabel {
  width: 100%;
  font-size: 18px;
  color: #8d8d8d;
  margin-top: 16px;
  margin-bottom: 6px;
  display: inline-block;
}

.eptextfield {
  width: 370px;
  height: 35px;
  font-size: 15px;
  border: 1.4px solid #c6c6c6;
  outline: none;
  background-color: #fff;
  border-radius: 5px;
  display: block;
  /* flex-wrap: wrap; */
  text-align: left;
  padding-left: 5px;
}

#btnsEp {
  /* display: inline-flex; */
  display: flex;
  flex-wrap: wrap;
  /* border: 2px solid royalblue; */
  /* flex-direction: row; */
}

.eps-button {
  width: 180px;
  height: 50px;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  background-color: #1dd1a1;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.17);
  border-radius: 5px;
  margin-top: 40px;
  display: inline-block;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0px;
  margin-right: 18px;
}

.epcl-button {
  width: 180px;
  height: 50px;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  background-color: #6d6d6d;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.17);
  border-radius: 5px;
  margin-top: 40px;
  display: inline-block;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0px;
}

#ep-right-page {
  width: 47%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
  background-color: rgb(230, 230, 230);
  color: #8d8d8d;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  /* box-sizing: border-box; */
  overflow-x: hidden;
}

#ep-right-page #ep-simbol {
  display: inline-block;
  margin: 0px 0px 0px 0px;
  font-size: 40px;
}

#ep-right-page #ep-title {
  display: inline-block;
  text-align: center;
  margin: 15px 10px 0px 10px;
  font-size: 18px;
}
