/* newproducts - flex container -parent element */

#newproducts {
  width: 100%;
  height: 100%;
  font-family: "Roboto", sans-serif;
  display: flex;
  justify-content: center;
  /* align-items: center;      */
  flex-wrap: wrap;
  align-content: center;
}

#npmain-container {
  width: 97%;
  height: calc(100% - 170px);
}

#npmaintitle {
  width: 90%;
  display: flex;
  /* justify-content: space-between; */
  align-items: center;
  margin: 20px auto 20px auto;
}

#npmaintitle h1 {
  font-size: 22px;
  font-weight: bold;
  color: #000000;
}

.np-form-container {
  display: flex;
  /* flex-direction: row; */
  align-items: center; /* check*/
  justify-content: center; /*check*/
  /* flex-wrap: wrap; */
  width: 73%;
  height: 650px;
  margin: 0 auto 40px 5%;
  box-sizing: border-box;
  overflow-x: hidden;
  /* background-color: red; */
  /* justify-content: space-between; */
}

@media screen and (max-width: 1024px) {
  .np-form-container {
    width: 90%;
  }
  .np-form-container #np-fpage {
    display: flex;
    padding: 0px 50px 0px 50px;
    width: 55%;
  }
  .np-form-container #np-right-page {
    width: 45%;
  }
  .np-form-container #np-fpage input {
    min-width: 0px;
    width: 100%;
  }
  #np-fpage #btnsNp {
    /* min-width: 0px;
    width: 100%;
    display: flex;
    flex-wrap: wrap; */
    display: flex;
    flex-direction: column;
  }
  /* #np-fpage #createP {
    min-width: 0px;
    width: 50%;
  }

  #np-fpage #closeNp {
    min-width: 0px;
    width: 50%;
  } */
  #np-fpage .cp-button {
    margin: 30px 0px 10px 0px;
    width: 100%;
  }
  #np-fpage .cl-button {
    margin: 20px 0px 10px 0px;
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .np-form-container {
    display: flex;
    height: 650px;
    width: 90%;
  }
  .np-form-container #np-fpage {
    display: flex;
    align-items: center;
    padding: 0px 50px 0px 50px;
    height: 650px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 10px;
    width: 63%;
  }
  .np-form-container #np-right-page {
    width: 37%;
    height: 650px;
    border-top-left-radius: 0px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0px;
  }
  .np-form-container #np-fpage input {
    width: 100%;
  }
  #np-fpage #btnsNp {
    display: flex;
    flex-direction: column;
  }

  #np-fpage .cp-button {
    margin: 30px 0px 10px 0px;
    width: 100%;
  }
  #np-fpage .cl-button {
    margin: 20px 0px 10px 0px;
    width: 100%;
  }
}
@media screen and (max-width: 426px) {
  #npmain-container {
    height: calc(100% - 280px);
  }
  .np-form-container {
    display: flex;
    flex-direction: column;
    height: 800px;
    width: 90%;
  }
  .np-form-container #np-fpage {
    display: flex;
    align-items: center;
    padding: 0px 50px 0px 50px;
    height: 650px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
    width: 100%;
  }
  .np-form-container #np-right-page {
    width: 100%;
    height: 150px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-left-radius: 10px;
  }
  #np-fpage input {
    /* min-width: 0px; */
    width: 100%;
  }
  #np-fpage #btnsNp {
    display: flex;
    /* align-items: center; */
    flex-direction: column;
  }
  #np-fpage .cp-button {
    margin: 30px 0px 10px 0px;
    width: 100%;
  }
  #np-fpage .cl-button {
    margin: 20px 0px 10px 0px;
    width: 100%;
  }
}
/*LEFT PAGE*/
#np-fpage {
  display: flex;
  /* float: left; */
  /* flex: 1 1 0%; */
  white-space: nowrap;
  align-items: center;
  /* justify-content: flex-start;  */
  /*flex-start*/
  width: 53%;
  height: 100%;
  background-color: #fff;
  box-sizing: border-box;
  padding: 0px 0px 0px 50px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  /* overflow-x: hidden; */
  overflow: hidden;
}

.nplabel {
  width: 100%;
  font-size: 18px;
  color: #8d8d8d;
  margin-top: 16px;
  margin-bottom: 6px;
  display: inline-block;
}

.nptextfield {
  /* width: 100%; */
  width: 370px;
  height: 35px;
  font-size: 15px;
  border: 1.4px solid #c6c6c6;
  outline: none;
  background-color: #fff;
  border-radius: 5px;
  display: block;
  /* flex-wrap: wrap; */
  text-align: left;
  padding-left: 5px;
}

#btnsNp {
  display: flex;
  flex-wrap: wrap;
}

.cp-button {
  width: 180px;
  height: 50px;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  background-color: #1dd1a1;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.17);
  border-radius: 5px;
  margin-top: 40px;
  margin-right: 18px;
  display: inline-block;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0px;
}

.cl-button {
  width: 180px;
  height: 50px;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  background-color: #6d6d6d;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.17);
  border-radius: 5px;
  margin-top: 40px;
  display: inline-block;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0px;
}

/*RIGHT PAGE*/
#np-right-page {
  width: 47%;
  height: 100%;
  display: flex;
  /* flex: 0 0 auto; */
  /* float: right; */
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
  background-color: rgb(230, 230, 230);
  color: #8d8d8d;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  /* box-sizing: border-box; */
  overflow-x: hidden;
}

#np-right-page #simbol {
  display: inline-block;
  margin: 0px 0px 0px 0px;
  font-size: 40px;
}

#np-right-page #title {
  display: inline-block;
  text-align: center;
  margin: 15px 10px 0px 10px;
  font-size: 18px;
}
